@import url('https://fonts.googleapis.com/css?family=Baloo+2:400,800&display=swap');

:root {
    --color-background: #ece5ce; /* Color de fondo general para el neumorfismo */
    --color-shadow-dark: rgba(0, 0, 0, 0.2); /* Sombra oscura para el efecto hundido */
    --color-shadow-light: rgba(255, 255, 255, 0.7); /* Sombra clara para el efecto elevado */
    --color-text: #333; /* Color del texto */
    --color-input-background: var(--color-background); /* Fondo del input */
    --border-radius: 11px; /* Radio del borde */
}

.word-box input {
    text-align: center !important;
    font-weight: bold;
    font-family: 'Baloo 2', cursive;
    color: var(--color-text);
    background-color: var(--color-input-background);
    padding: 20px;
    border-radius: var(--border-radius);
    border: none;
}

/* Eliminar estilos no deseados para limpieza */
.word-box input::before,
.MuiFilledInput-underline::after,
.MuiFilledInput-underline::before {
    display: none;
}

.MuiFilledInput {
    text-align: center !important;
}

/* Ajustes adicionales para mantener la consistencia y soportar animaciones */
.word-box div {
    border-radius: var(--border-radius);
}

/* Animación de temblor (shake) para retroalimentación */
@keyframes shake {
    0%, 100% { transform: translateX(0); }
    25% { transform: translateX(-10px); }
    50% { transform: translateX(10px); }
    75% { transform: translateX(-10px); }
}

.shake-animation {
    animation: shake 0.5s ease-in-out;
}

/* Animación flotante para atractivo visual */
/* @keyframes float {
    0%, 100% { transform: translateY(0px); }
    50% { transform: translateY(-20px); }
} */

.word-box {
    animation: float 5s ease-in-out infinite;
}